/* CSS RESET */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* CSS RESET */

#background-video {
  position: absolute;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  mix-blend-mode: color;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

.App {
  text-align: center;
  color: white;
  display: flex;
  justify-content: center;
}

.container {
  width: 45%;
  background-color: rgb(138, 138, 148);
  margin-top: 12.5%;
  padding: 2% 0 2.5% 0;
  border: 5px solid white;
  border-radius: 5%;
}

.display img {
  height: 60px;
  padding-top: 0.5rem;
}

.display h2 {
  padding-top: 0.5rem;
}

.display .pleaseEnter{
  padding-bottom: 1.5rem;
}
.display h4 {
  padding-top: 0.5rem;
}
.display p {
  padding-top: 0.5rem;
}
#time {
  padding-bottom: 1rem;
}

form label {
  color: black;
}

form {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  position: relative;
}

form ::placeholder {
  color: white;
  font-size: 1rem;
}

input {
  width: 35%;
  max-width: 280px;
  padding: 10px 15px;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 1rem 0px 1rem 0px;
  border-bottom: 0.2rem solid rgba(0, 0, 0, 0.3);
  color: #313131;
  font-size: 1rem;
  transition: all 0.3s ease-out;
}

input:focus {
  background-color: rgba(255, 255, 255, 0.6);
  border-color: rgba(0, 0, 0, 0.6);
  font-size: 1.25rem;
}
.searchbtn {
  padding-left: 0.5rem;
}

@media screen and (max-width: 768px) {
  .container {
    width: 55%;
  }
}

@media screen and (max-width: 425px) {
  .container {
    width: 75%;
    margin-top: 30%;
  }
  form ::placeholder {
    font-size: 0.85rem;
  }
}

@media screen and (max-width: 375px) {
  .container {
    width: 75%;
    margin-top: 35%;
  }
  form ::placeholder {
    font-size: 0.7rem;
  }
}
